[ng\:cloak], [ng-cloak], [data-ng-cloak], [x-ng-cloak], .ng-cloak, .x-ng-cloak {
  display: none !important;
}

body {
  background: #E3EBF4;
}

.container {
  width: 400px;
  margin: 100px auto;
  text-align: center;
}

button {
  border: none;
  outline: none;
  background: #404040;
  border-radius: 5%;
  font-size: 1em;
  text-align: center;
  color: white;
  box-shadow: 0px 5px #282828;
}
button:active {
  margin-top: 5px;
  box-shadow: 0px 0px 0px #282828;
}

.start {
  width: 150px;
  height: 50px;
}
